
// cookie
export function setCookie(name, value) {
  var Days = 30;
  var exp = new Date();
  exp.setTime(exp.getTime() + Days * 24 * 60 * 60 * 1000);
  document.cookie = name + "=" + escape(value) + ";expires=" + exp.toGMTString();
}
//cookie
export function getCookie(name) {
  var arr, reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
  if(arr = document.cookie.match(reg)){
    return unescape(arr[2]);
  }else{
    return null;
  }
}
//cookie
export function delCookie(name) { //cookie
  var exp = new Date();
  exp.setTime(exp.getTime() + (-1 * 24 * 60 * 60 * 1000));
  var cval = getCookie(name);
  document.cookie = name + "=" + cval + "; expires=" + exp.toGMTString();
}

export function objToUrl(obj) {
  if (!obj) {
    return '';
  }
  let params = [];
  for (const key in obj) {
    if (obj.hasOwnProperty(key)) {
      const element = obj[key];
      params.push(encodeURIComponent(key) + '=' + encodeURIComponent((element === undefined ? '' : element)));
    }
  }
  return params.join("&");
}

const phoneType=()=>{
  var browser={
    versions:function(){
    var u = navigator.userAgent, app = navigator.appVersion;
    return {
    trident: u.indexOf('Trident') > -1, //IE
    presto: u.indexOf('Presto') > -1, //opera
    webKit: u.indexOf('AppleWebKit') > -1, //
    gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') == -1,//
    mobile: !!u.match(/AppleWebKit.*Mobile.*/), //
    ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //ios
    android: u.indexOf('Android') > -1 || u.indexOf('Linux') > -1, //androiduc
    iPhone: u.indexOf('iPhone') > -1 , //iPhoneQQHD
    iPad: u.indexOf('iPad') > -1, //iPad
    webApp: u.indexOf('Safari') == -1, //web
    weixin: u.indexOf('MicroMessenger') > -1, // 2015-01-22
    qq: u.match(/\sQQ/i) == " qq" //QQ
    };
    }(),
    language:(navigator.browserLanguage || navigator.language).toLowerCase()
    }
    
    return browser;
}

